import axios from 'axios'

export default {
  state: {
    chat: null,
    chats: null,
    ws: null,
    wsStatus: 'none',
  },
  getters: {
  },
  mutations: {
    GET_CHAT: (state, chat) => {
      state.chat = chat
    },
    GET_CHATS: (state, chats) => {
      state.chats = chats
    },
    PUSH_MESSAGE: (state, message) => {
      state.chat.messages.push(message)
    },
    CONN_WEBSOCKET: (state, ws) => {
      state.ws = ws
      state.wsStatus = 'success'
      console.log('Connected')
    },
    ERROR_WEBSOKET: (state) => {
      state.wsStatus = 'error'
    },
    LOADING_WEBSOKET: (state) => {
      state.wsStatus = 'loading'
    },
    CLOSE_WEBSOCKET: (state) => {
      if (state.ws) {
        state.ws.close()
      }
      state.wsStatus = 'none'
    }
  },
  actions: {
    getChats ({ commit, state }) {
      axios.get(`/chats`)
        .then(function (resp) {
          commit('GET_CHATS', resp.data)
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getChat ({ commit, dispatch }, id) {
      dispatch('wsClose')
      commit('LOADING_WEBSOKET')

      axios.get(`/chats/` + id)
      .then(function (resp) {
        commit('GET_CHAT', resp.data)
        dispatch('wsConn', resp.data.identifier)
      })
      .catch((err) => {
        commit('ERROR_WEBSOCKET')
        console.log(err)
      })
    },
    wsConn ({ commit, state }, id) {
      const protocol = window.location.protocol === 'https:' ? 'wss' : 'ws'
      let ws = new WebSocket( protocol + "://localhost:4545/ws/conn?id=" + id)
      commit('CONN_WEBSOCKET', ws)
      state.ws.onclose = function (e) {
        console.log("Connection closed")
      }
      state.ws.onmessage = function (e) {
        var msg = JSON.parse(e.data)
        //console.log(msg)
        commit('PUSH_MESSAGE', msg)
      }
    },
    wsClose ({ commit, state }) {
      state.chat = null
      commit('CLOSE_WEBSOCKET')
    },
    wsSendMessage ({ commit, state }, message) {
      return new Promise((resolve, reject) => {
        if (state.ws) {
          state.ws.send(JSON.stringify(message))
          resolve()
        } else {
          commit('ERROR_WEBSOKET')
          reject()
        }
      })
    }
  }
}
