import axios from 'axios'

export default {
  state: {
    userCredentials: null,
    user: null,
    users: null,
    friends: null
  },
  getters: {
    usersWithFriendship: (state) => {
      let usersF = []
      if (state.users && state.friends) {
        state.users.forEach(user => {
          user.friendship = false
          state.friends.forEach(friend => {
            if (friend.id === user.id) {
              user.friendship = true
            }
          })
          usersF.push(user)
        })
      }
      return usersF
    }
  },
  mutations: {
    GET_USER_CREDENTIALS: (state, userCredentials) => {
      state.userCredentials = userCredentials
    },
    GET_USER: (state, user) => {
      state.user = user
    },
    GET_USERS: (state, users) => {
      state.users = users
    },
    GET_FRIENDS: (state, friends) => {
      state.friends = friends
    }
  },
  actions: {
    getUsers ({ commit, state }) {
      axios.get(`/users`)
        .then(function (resp) {
          commit('GET_USERS', resp.data)
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getUser ({ commit, state }, id) {
      axios.get(`/users/` + id)
        .then(function (resp) {
          commit('GET_USER', resp.data)
        })
        .catch((err) => {
          console.log(err)
        })
    },
    getFriends ({ commit, state }) {
      axios.get(`/users/friends`)
        .then(function (resp) {
          commit('GET_FRIENDS', resp.data)
        })
        .catch((err) => {
          console.log(err)
        })
    },
    addFriend ({ commit, state }, id) {
      return new Promise((resolve, reject) => {
        axios.get(`/users/friends/` + id + `/add`)
          .then(function (resp) {
            resolve(resp.data)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    deleteFriend ({ commit, state }, id) {
      return new Promise((resolve, reject) => {
        axios.delete(`/users/friends/` + id + `/delete`)
          .then(function (resp) {
            resolve(resp.data)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getUserCredentials ({ commit, state }) {
      axios.get(`/users/info`)
        .then(function (resp) {
          commit('GET_USER_CREDENTIALS', resp.data)
        })
        .catch((err) => {
          console.log(err)
        })
    },
  }
}
