import Vue from 'vue'
import axios from 'axios'

export default {
  state: {
    authStatus: 'none',
    token: '',
    expiresIn: '',
    authError: ''
  },
  getters: {
    isAuth: state => !!state.token,
    authStatus: state => state.authStatus,
    authError: state => state.authError
  },
  mutations: {
    AUTH_REQUEST: (state) => {
      state.authStatus = 'loading'
    },
    AUTH_SUCCESS: (state, token, expiresIn) => {
      state.authStatus = 'success'
      state.token = token
      state.expiresIn = expiresIn
    },
    AUTH_ERROR: (state, error) => {
      state.authStatus = 'error'
      state.authError = error
      state.token = ''
      state.expiresIn = ''
    },
    AUTH_LOGOUT: (state) => {
      state.expiresIn = ''
      state.token = ''
      state.authStatus = 'none'
    }
  },
  actions: {
    signIn ({ commit }, data) {
      return new Promise((resolve, reject) => {
        commit('AUTH_REQUEST')
        axios.post(`/auth/sign_in`, JSON.stringify(data))
          .then(function (resp) {
            const accessToken = resp.data.access_token
            const expiresIn = resp.data.access_expires_in
            const refreshToken = resp.data.refresh_token
            Vue.cookie.set('access_token', accessToken)
            Vue.cookie.set('expires_in', expiresIn)
            Vue.cookie.set('refresh_token', refreshToken)

            axios.defaults.headers.common['Authorization'] = 'Bearer ' + accessToken
            commit('AUTH_SUCCESS', accessToken, expiresIn)
            resolve(resp)
          })
          .catch(err => {
            Vue.cookie.delete('access_token')
            Vue.cookie.delete('expires_in')
            Vue.cookie.delete('refresh_token')
            delete axios.defaults.headers.common['Authorization']
            let error = 'Internal server error'
            if (err.response) {
              if (err.response.status === 401) {
                error = 'Wrong username or password'
              }
            }
            commit('AUTH_ERROR', error)
            reject(err)
          })
      })
    },
    signUp ({ commit }, data) {
      return new Promise((resolve, reject) => {
        commit('AUTH_REQUEST')
        axios.post(`/auth/sign_up`, JSON.stringify(data))
          .then(function (resp) {
            const accessToken = resp.data.access_token
            const expiresIn = resp.data.access_expires_in
            const refreshToken = resp.data.refresh_token
            Vue.cookie.set('access_token', accessToken)
            Vue.cookie.set('expires_in', expiresIn)
            Vue.cookie.set('refresh_token', refreshToken)

            axios.defaults.headers.common['Authorization'] = 'Bearer ' + accessToken
            commit('AUTH_SUCCESS', accessToken, expiresIn)
            resolve(resp)
          })
          .catch(err => {
            Vue.cookie.delete('access_token')
            Vue.cookie.delete('expires_in')
            Vue.cookie.delete('refresh_token')
            delete axios.defaults.headers.common['Authorization']
            let error = 'Internal server error'
            if (err.response) {
              if (err.response.data) {
                error = err.response.data.message
              }
            }
            commit('AUTH_ERROR', error)
            reject(err)
          })
      })
    },
    logout () {
      Vue.cookie.delete('access_token')
      Vue.cookie.delete('expires_in')
      Vue.cookie.delete('refresh_token')
      delete axios.defaults.headers.common['Authorization']
      window.location.reload()
    }
  }
}
